body {
  background: url(bubble_wallpaper_simple_background.jpg) no-repeat center center fixed;
  background-size: cover;
}

.App {
  padding: 50px 20px 20px 20px;
}


.Editor {
 //background-color: #CB7076;
 background-color: rgba(112,128,144,0.8);
 border: 2px solid;
 resize:vertical;
 display:block;
 font-size:14px;
}

.Editor.half {
  width: 47%;
  float: left;
  height: 566px;
//  width: 40em;
}

.Editor.full {
  width: 100%;
  float: left;
  height: 566px;
//  width: 40em;
}
.Editor.min {
  width: 100%;
  float: left;
  height: 3px;
//  width: 40em;
}

.Previewer {
  margin-left: 53%;
//  background-color: #774A69;
  background-color: rgba(112,128,144,0.8);
  padding: 10px;
  border: 2px solid;
  max-height: 566px;
  overflow: auto;
}
.label {
  display:block;
}

::-webkit-scrollbar {
    width: 12px;
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
//    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
//    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}



